import React from 'react';
import Block from './Block';
import { formatPrice } from '../utility/format';
import Badge from './Badge';

import LargeChevron from '../data/icon/large-chevron';
import PersonIcon from '../data/icon/person';

import './TicketResult.css';

export default class TicketResult extends React.Component {
  getBadges() {
    const { data } = this.props;

    const elements = [];

    if (data.isCheapest) {
      elements.push(<Badge key="cheapest" cheapest text="Cheapest" />);
    }

    if (data.isRecommended) {
      elements.push(<Badge key="recommended" recommended text="Recommended" />);
    }

    if (elements.length > 0) {
      return (
        <div className="TicketResult-badges">
          {elements}
        </div>
      );
    }

    return null;
  }

  getPrice() {
    const { data } = this.props;

    if (data.isDiscounted) {
      return (
        <div className="TicketResult-discount">
          <div className="TicketResult-discount-info">
            <div className="TicketResult-discount-label">
              Discounted
            </div>
            <div className="TicketResult-price">
              {formatPrice(data.price)}
            </div>
            <div className="TicketResult-price-strike">
              {formatPrice(data.price * 1.2)}
            </div>
          </div>
          <LargeChevron className="TicketResult-chevron" />
        </div>
      );
    }

    return (
      <div className="TicketResult-nodiscount">
        <div className="TicketResult-price">
          {formatPrice(data.price)}
        </div>
        <LargeChevron className="TicketResult-chevron" />
      </div>
    );

  }

  render() {
    const { className, data, onClick, onConditionsClick, onPriceBreakdownClick } = this.props;
    
    return (
      <Block className="TicketResult" onClick={this.handleClick}>
        <div className="TicketResult-top">
          <div className="TicketResult-top-left">
            {this.getBadges()}
            <div className="TicketResult-title">
              {data.title}
            </div>
            <div className="TicketResult-description">
              {data.descriptionLines.map((line, index) => (
                <div key={index} className="TicketResult-description-line">
                  {line}
                </div>
              ))}
            </div>
          </div>
          <div className="TicketResult-top-right">
            {this.getPrice()}
          </div>
        </div>
        <div className="TicketResult-bottom">
          <div className="TicketResult-action" onClick={e => { onConditionsClick(e); e.stopPropagation() }}>
            Ticket restrictions
          </div>
          <div className="TicketResult-action" onClick={e => { onPriceBreakdownClick(e); e.stopPropagation() }}>
            Price details
          </div>
        </div>
      </Block>
    );

    return (
      <Block className={className} onClick={onClick}>
        <div className="TicketResult">
          <div className="TicketResult-top">
            <div className="TicketResult-topLine">
              {this.getBadges()}
              {data.price.standard && <div className="ServiceResult-priceLabel">
                <PersonIcon className="ServiceResult-personIcon" />
                <b>{data.passengers}&nbsp;</b>
                Return from
              </div>}
            </div>
            <div className="TicketResult-top-content">
              <div className="TicketResult-priceLabel">
                {data.passengers}
                <PersonIcon className="TicketResult-personIcon" />
              </div>
              <div className="TicketResult-header">
                <div className="TicketResult-title">
                  {data.title}
                </div>
                <div className="TicketResult-price">
                  {formatPrice(data.price)}
                </div>
              </div>
              <div className="TicketResult-sub">
                <div className="TicketResult-note">
                  {data.descriptionLines.map((line, index) => <div key={index} className="TicketResult-note-line">{line}</div>)}
                </div>
              </div>
            </div>
            <LargeChevron className="TicketResult-chevron" />
          </div>
          <div className="TicketResult-actions">
            <div className="TicketResult-action" onClick={e => { onConditionsClick(e); e.stopPropagation() }}>
              Ticket conditions
            </div>
            <div className="TicketResult-action" onClick={e => { onPriceBreakdownClick(e); e.stopPropagation() }}>
              Price breakdown
            </div>
          </div>
        </div>
      </Block>
    )
  }
}
